<template>
    <div class="wrap" >
        <div class="pay-top">
            <div class="pay-money">
                <p>支付金额</p>
                <span>￥1220.00</span>
            </div>
            <div class="pay-info">
                <p class="name">供应商名称<span>广州医药公司大众分公司</span></p>
                <p class="order-num">订单号码<span>255611111002</span></p>
            </div>
        </div>
        <div class="pay-bottom">
            <div class="pay-type-title">
                <i></i>选择支付方式<i></i>
            </div>
            <div class="pay-type-main">
                <div class="pay-wx"><i></i>微信支付</div>
                <div class="pay-zfb"><i></i>支付宝支付</div>
                <div class="pay-phone">一键手机支付</div>
                <img :src="imgSrc" alt="">
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "order-pay",
        data() {
            return {
                imgSrc: ''
            }
        },
        created() {
            let timeStamp = new Date().getTime();
            let params = {'directpaytype': 1, 'orderId': 914};
            let md5 = this._cryptojs.MD5(JSON.stringify(params));
            // 订单信息
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/encryption/aes',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                data: {
                    'time-stamp': timeStamp,
                    "requestBody": JSON.stringify(params)
                }
            }).then((res) => {
                let aes = res.data.data.aesRequestBody;
                this.$http({
                    method: 'post',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    url: this._ajaxUrl + '/pay/loadQRCode',
                    data: aes
                }).then((res) => {
                    console.log(res.data)
                    this.imgSrc = 'data:image/jpeg;base64,' + res.data;
                })
            })
        }
    }
</script>

<style lang="less" scoped>
    .wrap{
        width: 100%;
        height: 1334px;
        overflow: hidden;
        background: #f6f6f6;
        .pay-top{
            width: 100%;
            background: #fff;
            padding: 0 30px;
            .pay-money{
                width: 100%;
                border-bottom: 2px dashed #ccc;
                text-align: center;
                padding-bottom: 48px;
                p{
                    font-size: 34px;
                    color: #222;
                    padding-top: 38px;
                    margin-bottom: 48px;
                }
                span{
                    font-size: 48px;
                    color: #f82222;

                }
            }
            .pay-info{
                width: 100%;
                .name{
                    padding-top: 22px;
                    font-size: 28px;
                    color: #666;
                    padding-bottom: 22px;
                }
                .order-num{
                    font-size: 28px;
                    color: #666;
                    padding-bottom: 28px;
                }
                span{
                    float: right;
                }
            }
        }
        .pay-bottom{
            margin-top: 16px;
            /*height: 1882px;*/
            height: 100%;
            width: 100%;
            background: #fff;
            padding: 0 30px;
            .pay-type-title{
                height: 72px;
                width: 100%;
                font-size: 22px;
                color: #999;
                text-align: center;
                display: flex;
                align-items: center;
                justify-content: center;
                i{
                    width: 64px;
                    height: 5px;
                    display: inline-block;
                    background: url("../../assets/icon/icon-pay-type.png") no-repeat;
                    background-size: contain;
                    margin: 0 8px;
                }
                i:nth-last-child(1){
                    transform: rotateZ(180deg);
                }
            }
            .pay-type-main{
                .pay-wx,.pay-zfb,.pay-phone{
                    i{
                        width: 62px;
                        height: 54px;
                        display: inline-block;
                        background: url("../../assets/icon/icon-pay-wx.png") no-repeat;
                        background-size: contain;
                        margin-right: 16px;
                    }
                    /*border: 2px solid #ccc;*/
                    box-shadow: 0 0 0 2px #ccc;
                    margin-bottom: 32px;
                    border-radius: 10px;
                    height: 88px;
                    color: #222;
                    font-size: 34px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                .pay-zfb{
                    padding-left: 34px;
                    i{
                        width: 54px;
                        background: url("../../assets/icon/icon-pay-zfb.png") no-repeat;
                        background-size: contain;
                    }
                }
                .pay-phone{
                    box-shadow: none;
                    background: #f82222;
                    border-radius: 10px;
                    color: #fff;
                }
            }
        }
    }
</style>


