<template>
    <div class="wrap">
        <!-- 网络异常 -->
        <div class="network-error" v-if="netWorkError">
            <div class="btn-refresh" @click="$router.go(0)">刷新</div>
        </div>

        <!-- 未登录 -->
        <div class="login-no" v-else-if="isLogin">
            <router-link class="btn-login" to="/login">立即登陆</router-link>
        </div>

        <!-- 购物车为空 -->
        <div class="shop-empty" v-else-if="shopEmpty">
            <router-link to='/'>去采购吧~</router-link>
        </div>

        <!-- 购物车有东西 -->
        <div class="shop-view" v-else>
            <div class="shop-ctn">
                <!-- 头部 -->
                <div class="shop-header">
                    <div class="header-text">购物车({{productAmount}})</div>
                    <div class="btn" @click="headerBtn($event)">{{headerBtnText}}</div>
                </div>

                <!-- 订单 -->
                <div class="order" v-for="(order, orderIdx) in orderArr">
                    <!-- 订单头部 -->
                    <div class="order-header"
                        :class="{'active': order.firmSelectActive}"
                        @click="firmSelectFn(orderIdx)">
                        <span class="order-firm">{{order.thirdStoreInfoAll.companyName}}</span>
                        <span class="order-people">{{order.thirdStoreInfoAll.bussLegalName}}</span>
                    </div>

                    <!-- 订单提示 -->
                    <div class="order-tip">订单起配金额需满¥{{order.thirdStoreInfoAll.storeMinimumMoney}}元&nbsp订单满&nbsp¥{{order.thirdStoreInfoAll.freeMoney}}元免运费，&nbsp不满收取&nbsp¥{{order.thirdStoreInfoAll.collectMoney}}元运费</div>
                    <!-- 订单内容 -->
                    <div class="order-ctn">
                        <!-- 订单详情 product, productIdx-->
                        <div class="order-detail" v-for="(product, productIdx) in order.listShoppingCart">
                            <!-- 产品选择 -->
                            <div class="product-select"
                                :class="{'active': product.productSelectActive}"
                                @click="productSelectFn(orderIdx, productIdx)">
                                <input name="" type="checkbox"
                                    :value="product.shoppingCartId"
                                    v-model="product.productSelectActive">
                            </div>

                            <!-- 产品详情 -->
                            <div class="product-detail">
                                <!-- 产品图片 -->
                                <router-link class="product-img" :to="{path: '/goodsDetail', query: {goodsId: product.goodsId}}">
                                    <img :src="product.goodsInfoAll.goodsImg">
                                    <!-- <p class="warm-tip" v-show="!product.goodsProductLot || product.inventoryShow">(库存不足，剩余{{product.goodsProductLot ? product.goodsProductLot.goodsProductLotWareStock : 0}}盒)</p> -->
                                    <p class="warm-tip" v-show="goodsProductLotTip(product)">(库存不足，剩余{{product.goodsProductLot ? product.goodsProductLot.goodsProductLotWareStock : 0}}盒)</p>
                                </router-link>

                                <!-- 产品信息 -->
                                <div class="product-infor">
                                    <router-link :to="{path: '/goodsDetail', query: {goodsId: product.goodsId}}">
                                        <div class="infor-div">
                                            <!-- 产品名字 -->
                                            <div class="product-name">{{product.goodsName}}</div>
                                            <!-- 产品价格 -->
                                            <div class="product-price"><span>{{productPriceInteger(orderIdx, productIdx)}}</span>{{productPriceDecimals(orderIdx, productIdx)}}</div>
                                        </div>

                                        <!-- 其他信息 -->
                                        <ul class="infor-other">
                                            <!-- 产品规格 -->
                                            <li>{{product.specName}}</li>
                                            <!-- 产品生产公司 -->
                                            <li>{{product.goodsInfoAll.producer}}</li>
                                            <!-- 产品批号 -->
                                            <li>批号：{{product.approvalNumber}}</li>
                                        </ul>
                                    </router-link>

                                    <!-- 产品数量 -->
                                    <div class="product-amount">
                                        <span class="amount-label">{{product.shoppingType === 1 ? '拆零' : '整件'}}:</span>
                                        <span class="btn-amount amount-reduce"
                                            @click="amountReduceFn(orderIdx, productIdx)">-</span>
                                        <input class="amount-input" type="tel" v-model="product.shoppingType === 1 ? product.goodsOpenNum : product.goodsCompleteNum"
                                            @change="amountInputFn($event, orderIdx, productIdx)">
                                        <span class="btn-amount amount-add"
                                            @click="amountAddFn(orderIdx, productIdx)">+</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>        
                </div>
            </div>

            <!-- 操作栏 -->
            <div class="shop-opt">
                <div class="select-all"
                    :class="{'active': allSelectActive}"
                    @click="allSelectFn()">全选</div>
                <!-- 结算 -->
                <div class="go-buy" v-if="goBuy">
                    <div class="total">总价：<span class="total-price">￥<span>{{totalPriceInteger}}</span>.{{totalPriceDecimals}}</span></div>
                    <div class="btn-buy" @click="buyFn()">去结算</div>
                </div>
                <!-- 编辑 -->
                <div class="go-edit " v-else>
                    <div class="btn btn-del" @click="delPopShowFn()">删除</div>
                    <div class="btn btn-collect" @click="collectFn()">移入收藏夹</div>
                </div>
            </div>

            <!-- 导航 -->
            <tabbar></tabbar>
        </div>

        <!-- 弹窗-删除 -->
        <transition name="fade">
            <div class="pop-shade" v-show="popShow">
                <!-- 弹窗主体 -->
                <div class="pop-main">
                    <div class="pop-body">
                        <p>确定要删除这件商品吗？</p>
                    </div>
                    <div class="pop-footer">
                        <div class="btn btn-yes" @click="delFn()">确定</div>
                        <div class="btn btn-no" @click="delPopHideFn()">取消</div>
                    </div>
                </div>
            </div>
        </transition>

    </div>
</template>

<script>
import tabbar from '../subcom/tab-bar';
import Vue from 'vue';
import {Toast} from 'mint-ui';
export default {
    name: "shop-car",
    components: {
        tabbar
    },
    data() {
        return {
            // 网络异常
            netWorkError: false,
            // 是否登陆
            isLogin: false,
            // 购物车是否空
            shopEmpty: false,
            // 去结算or编辑
            goBuy: true,
            // 全选
            allSelectActive: false,
            // 订单
            orderArr: [],
            // 总价
            totalPrice: '0.00',
            // 弹窗显示or隐藏
            popShow: false
        }
    },
    created() {
        this.shopData();
    },
    computed: {
        // 头部按钮文字
        headerBtnText: function () {
            if (this.goBuy) {
                return '编辑';
            } else {
                return '完成';
            }
        },
        // 总价整数部分
        totalPriceInteger: function () {
            return this.totalPrice.split('.')[0];
        },
        // 总价小数部分
        totalPriceDecimals: function () {
            return this.totalPrice.split('.')[1];
        }
    },
    methods: {
        // 刷新
        // refresh: function () {
        //     this.$router.go(0);
        // },
        // 购物车数据
        shopData: function () {
            let timeStamp = new Date().getTime();
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/shoppingcart/shoppingCartSaveController/selectShoppingCartInfoAll',
                headers: {
                  'time-stamp': timeStamp
                },
            }).then((res) => {
                // console.log(res.data)
                // 用户未登陆
                if (res.data.errorCode === 'login_0004') {
                    this.isLogin = true;
                } else {
                    this.isLogin = false;
                    // 清除原本的数据
                    this.orderArr.splice(0, this.orderArr.length);
                    // 后端返回的订单数据,没数据返回网络异常
                    if (res.data.data.list) {
                        this.orderArr.push(...res.data.data.list);
                        // console.log(this.orderArr);
                        if (this.orderArr.length <= 0) {
                            this.shopEmpty = true;
                        } else {
                            this.shopEmpty = false;
                            // 商品数量
                            let productAmount = 0;
                            this.orderArr.forEach(order => {
                                productAmount += order.listShoppingCart.length;
                            });
                            this.productAmount = productAmount;
                        }
                    } else {
                        this.netWorkError = true;
                    }
                }
            })
        },
        // 总价
        totalFn: function () {
            let totalPrice = 0;
            this.orderArr.forEach((orderVal) => {
                orderVal.listShoppingCart.forEach((goodsVal) => {
                    // 是否选择且有批次，如果选择了才计算价格
                    if (goodsVal.productSelectActive && goodsVal.goodsProductLimitPrice) {
                        // 1是拆零，2是整件
                        if (goodsVal.shoppingType === 1) {
                            totalPrice += goodsVal.goodsProductLimitPrice.openMoney * goodsVal.goodsOpenNum;
                        } else {
                            totalPrice += goodsVal.goodsProductLimitPrice.completeMoney * goodsVal.goodsCompleteNum;
                        }
                    }
                });
            });
            this.totalPrice = totalPrice.toFixed(2) + '';
        },
        // 头部按钮。编辑or完成
        headerBtn: function (event) {
            if (this.goBuy) {
                this.goBuy = false;
            } else {
                this.goBuy = true;
                // 如果产品被全部删除了，显示购物车为空
                if (this.orderArr.length == 0) {
                    this.shopEmpty = true;
                }
            }
            // 取消勾选
            this.allSelectActive = false;
            for (let i = 0, orderLen = this.orderArr.length; i < orderLen; i++) {
                this.orderArr[i].firmSelectActive = false;
                for (let j = 0, listShoppingCartLen = this.orderArr[i].listShoppingCart.length; j < listShoppingCartLen; j++) {
                    Vue.set(this.orderArr[i].listShoppingCart[j], 'productSelectActive', false);
                }
            }
            this.totalFn();
        },
        // 所有勾选的，有空需要优化。
        // 商品选择
        productSelectFn: function (parentIdx, selfIdx) {
            // 勾选商品
            Vue.set(this.orderArr[parentIdx].listShoppingCart[selfIdx], 'productSelectActive', !this.orderArr[parentIdx].listShoppingCart[selfIdx].productSelectActive);
            // 商品的数量
            let listShoppingCartLen = this.orderArr[parentIdx].listShoppingCart.length;
            // 选择的产品数量
            let productSelectActiveLen = 0;
            for (let i = 0; i < listShoppingCartLen; i++) {
                if (this.orderArr[parentIdx].listShoppingCart[i].productSelectActive) {
                    productSelectActiveLen++;
                }
            }
            // 如果选择的商品数量=商品的数量，该商家也勾选上
            if (productSelectActiveLen === listShoppingCartLen) {
                this.orderArr[parentIdx].firmSelectActive = true;
            } else {
                this.orderArr[parentIdx].firmSelectActive = false;
            }
            // 商家的数量
            let orderLen = this.orderArr.length;
            // 选择的商家数量
            let firmSelectActiveLen = 0;
            for (let i = 0; i < orderLen; i++) {
                if (this.orderArr[i].firmSelectActive) {
                    firmSelectActiveLen++;
                }
            }
            // 如果选择的商家数量=商家的数量，则全选
            if (firmSelectActiveLen === orderLen) {
                this.allSelectActive = true;
            } else {
                this.allSelectActive = false;
            }
            this.totalFn();
        },
        // 商家选择
        firmSelectFn: function (selfIdx) {
            // 勾选商家
            Vue.set(this.orderArr[selfIdx], 'firmSelectActive', !this.orderArr[selfIdx].firmSelectActive);
            // 如果商家勾选了，勾选商家下的所有商品。反之，取消。
            if (this.orderArr[selfIdx].firmSelectActive) {
                for (let i = 0, listShoppingCartLen = this.orderArr[selfIdx].listShoppingCart.length; i < listShoppingCartLen; i++) {
                    Vue.set(this.orderArr[selfIdx].listShoppingCart[i], 'productSelectActive', true);
                }
            } else {
                for (let i = 0, listShoppingCartLen = this.orderArr[selfIdx].listShoppingCart.length; i < listShoppingCartLen; i++) {
                    Vue.set(this.orderArr[selfIdx].listShoppingCart[i], 'productSelectActive', false);
                }
            }
            // 商家的数量
            let orderLen = this.orderArr.length;
            // 选择的商家数量
            let firmSelectActiveLen = 0;
            for (let i = 0; i < orderLen; i++) {
                if (this.orderArr[i].firmSelectActive) {
                    firmSelectActiveLen++;
                }
            }
            // 如果选择的商家数量=商家的数量，则全选
            if (firmSelectActiveLen === orderLen) {
                this.allSelectActive = true;
            } else {
                this.allSelectActive = false;
            }
            this.totalFn();
        },
        // 全选
        allSelectFn: function () {
            // 全选
            this.allSelectActive = !this.allSelectActive;
            // 全选所有商家and产品，或者取消。
            if (this.allSelectActive) {
                for (let i = 0, orderLen = this.orderArr.length; i < orderLen; i++) {
                    this.orderArr[i].firmSelectActive = true;
                    for (let j = 0, listShoppingCartLen = this.orderArr[i].listShoppingCart.length; j < listShoppingCartLen; j++) {
                        this.orderArr[i].listShoppingCart[j].productSelectActive = true;
                    }
                }
            } else {
                for (let i = 0, orderLen = this.orderArr.length; i < orderLen; i++) {
                    this.orderArr[i].firmSelectActive = false;
                    for (let j = 0, listShoppingCartLen = this.orderArr[i].listShoppingCart.length; j < listShoppingCartLen; j++) {
                        this.orderArr[i].listShoppingCart[j].productSelectActive = false;
                    }
                }
            }
            this.totalFn();
        },
        // 产品价格整数部分
        productPriceInteger: function (parentIdx, selfIdx) {
            // 判断是否有这个批次
            if (this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsProductLimitPrice) {
                let productPrice = '';
                // 判断是拆零还是整件，1是拆零2是整件
                if (this.orderArr[parentIdx].listShoppingCart[selfIdx].shoppingType === 1) {
                    productPrice += this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsProductLimitPrice.openMoney;
                } else {
                    productPrice += this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsProductLimitPrice.completeMoney;
                }
                return '￥' + (productPrice.split('.')[0] || '00');
            } else {
                return '';
            }
        },
        // 产品价格小数部分
        productPriceDecimals: function (parentIdx, selfIdx) {
            // 判断是否有这个批次
            if (this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsProductLimitPrice) {
                let productPrice = '';
                // 判断是拆零还是整件，1是拆零2是整件
                if (this.orderArr[parentIdx].listShoppingCart[selfIdx].shoppingType === 1) {
                    productPrice += this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsProductLimitPrice.openMoney;
                } else {
                    productPrice += this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsProductLimitPrice.completeMoney;
                }
                return '.' + (productPrice.split('.')[1] || '00');
            } else {
                return '';
            }
        },
        // 数量变化ajax。传入购物车id，数量，以及对应数据的idx值
        amountAjaxFn: function (shoppingCartId, amountVal, parentIdx, selfIdx) {
            let timeStamp = new Date().getTime();
            let params = {'shoppingCartId': shoppingCartId, 'goodsNum': amountVal}
            let md5 = this._cryptojs.MD5(JSON.stringify(params));
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/encryption/aes',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                data: {
                    "timeStamp": timeStamp,
                    "requestBody": JSON.stringify(params)
                }
            }).then((res) => {
                let aes = res.data.data.aesRequestBody;
                this.$http({
                    method: 'post',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    url: this._ajaxUrl + '/shoppingcart/shoppingCartSaveController/editShoppingGoodsNumber',
                    data: aes
                }).then((res) => {
                    // 后端返回大于0就是成功，否则失败
                    if (res.data > 0) {
                        // 判断是否有这个批次
                        // if (this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsProductLot) {
                            // 库存
                            let inventory = 0;
                            // 判断是拆零还是整件，1是拆零2是整件
                            if (this.orderArr[parentIdx].listShoppingCart[selfIdx].shoppingType === 1) {
                                this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsOpenNum = amountVal;
                                // 拆零库存直接就获取总数
                                inventory = parseInt(this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsProductLot.goodsProductLotWareStock, 10);
                            } else {
                                this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsCompleteNum = amountVal;
                                // 整件库存需要除以件装量
                                inventory = parseInt(this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsProductLot.goodsProductLotWareStock / this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsProductLimitPrice.pieceLoading, 10);
                            }
                            // 如果大于剩余的库存，显示库存不足提示
                            // Vue.set(this.orderArr[parentIdx].listShoppingCart[selfIdx], 'inventoryShow' , amountVal > inventory);
                            // 如果该商品是被勾选的，联动总价的变化
                            if (this.orderArr[parentIdx].listShoppingCart[selfIdx].productSelectActive) {
                                this.totalFn();
                            }
                        // } else {
                            // Vue.set(this.orderArr[parentIdx].listShoppingCart[selfIdx], 'inventoryShow', true);
                        // }
                    }
                })
            })
        },
        // 数量输入框
        amountInputFn: function (event, parentIdx, selfIdx) {
            // 购物车中的商品id
            let shoppingCartId = this.orderArr[parentIdx].listShoppingCart[selfIdx].shoppingCartId;
            // 采购数量
            let val = event.target.value;
            let amountVal = parseInt(val.replace(/\D/g,'').substr(0, 6), 10);
            if (amountVal <= 1) {
                amountVal = 1;
            }
            if (amountVal >= 999999) {
                amountVal = 999999;
            }
            this.amountAjaxFn(shoppingCartId, amountVal, parentIdx, selfIdx);
        },
        // 数量减少
        amountReduceFn: function (parentIdx, selfIdx) {
            // 购物车中的商品id
            let shoppingCartId = this.orderArr[parentIdx].listShoppingCart[selfIdx].shoppingCartId;
            // 采购数量
            let amountVal = 0;
            // 判断是拆零还是整件，1是拆零2是整件
            if (this.orderArr[parentIdx].listShoppingCart[selfIdx].shoppingType === 1) {
                amountVal = this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsOpenNum;
            } else {
                amountVal = this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsCompleteNum;
            }
            amountVal = amountVal - 1;
            if (amountVal <= 1) {
                amountVal = 1;
            }
            this.amountAjaxFn(shoppingCartId, amountVal, parentIdx, selfIdx);
        },
        // 数量增加
        amountAddFn: function (parentIdx, selfIdx) {
            // 购物车中的商品id
            let shoppingCartId = this.orderArr[parentIdx].listShoppingCart[selfIdx].shoppingCartId;
            // 采购数量
            let amountVal = 0;
            // 判断是拆零还是整件，1是拆零2是整件
            if (this.orderArr[parentIdx].listShoppingCart[selfIdx].shoppingType === 1) {
                amountVal = this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsOpenNum;
            } else {
                amountVal = this.orderArr[parentIdx].listShoppingCart[selfIdx].goodsCompleteNum;
            }
            amountVal = amountVal + 1;
            if (amountVal >= 999999) {
                amountVal = 999999;
            }
            this.amountAjaxFn(shoppingCartId, amountVal, parentIdx, selfIdx);
        },
        // 库存提示
        goodsProductLotTip: function (product) {
            // console.log(product);
            // 库存
            let goodsProductLotWareStock = product.goodsProductLot ? product.goodsProductLot.goodsProductLotWareStock : 0;
            // 采购数量
            let goodsNum = product.shoppingType === 1 ? product.goodsOpenNum : product.goodsCompleteNum;
            // 判断采购数量是否超过库存数量
            if (goodsNum > goodsProductLotWareStock) {
                Vue.set(product, 'inventoryShow', true);
                return true;
            } else {
                Vue.set(product, 'inventoryShow', false);
                return false;
            }
        },
        // 去结算
        buyFn: function () {
            // 选择的产品数量
            let productSelectActiveLen = 0;
            // 库存提示
            let inventoryTipLen = 0;
            let shoppingIds = '';
            for (let i = 0, orderLen = this.orderArr.length; i < orderLen; i++) {
                for (let j = 0, listShoppingCartLen = this.orderArr[i].listShoppingCart.length; j < listShoppingCartLen; j++) {
                    if (this.orderArr[i].listShoppingCart[j].productSelectActive) {
                        shoppingIds += this.orderArr[i].listShoppingCart[j].shoppingCartId + ',';
                        productSelectActiveLen++;
                        // 没库存或库存不足
                        if (!this.orderArr[i].listShoppingCart[j].goodsProductLot || this.orderArr[i].listShoppingCart[j].inventoryShow) {
                            inventoryTipLen++;
                        }
                    }
                }
            }
            // 如果选择的商品数量不是0且库存充足，可以去结算
            if (productSelectActiveLen != 0 && inventoryTipLen == 0) {
                shoppingIds = shoppingIds.substr(0, shoppingIds.length - 1);
                this.$router.push({
                    path: '/orderSubmit',
                    query: {
                        'shoppingIds': shoppingIds,
                        'shoppingCartType': 0,
                    }
                });
            } else {
                //提示语
                Toast({
                    message: '请选择库存充足的商品',
                    position: 'bottom',
                    duration: 3000,
                    className: 'public'
                });
            }
        },
        // 删除弹窗-显示
        delPopShowFn: function () {
            // 是否有选择商品
            var productSelectActive = this.orderArr.some(order => {
                return order.listShoppingCart.some(product => {
                    return product.productSelectActive
                })
            });
            if (productSelectActive) {
                this.popShow = true;
            } else {
                //提示语
                Toast({
                    message: '请选择商品',
                    position: 'bottom',
                    duration: 3000,
                    className: 'public'
                });
            }
        },
        // 删除弹窗-隐藏
        delPopHideFn: function () {
            this.popShow = false;
        },
        // 删除商品
        delFn: function () {
            // 选中的购物车id
            let shoppingIds = '';
            this.orderArr.forEach(order => {
                order.listShoppingCart.forEach(product => {
                    if (product.productSelectActive) {
                        shoppingIds += product.shoppingCartId + ',';
                    }
                })
            });
            let timeStamp = new Date().getTime();
            let params = {'shoppingIds': shoppingIds.substr(0 , shoppingIds.length - 1)}
            let md5 = this._cryptojs.MD5(JSON.stringify(params));
            this.$http({
                method: 'post',
                url: this._ajaxUrl + '/encryption/aes',
                headers: {
                    'time-stamp': timeStamp,
                    'data-signature': md5
                },
                data: {
                    "timeStamp": timeStamp,
                    "requestBody": JSON.stringify(params)
                }
            }).then((res) => {
                let aes = res.data.data.aesRequestBody;
                this.$http({
                    method: 'post',
                    headers: {
                        "time-stamp": timeStamp,
                        'data-signature': md5
                    },
                    url: this._ajaxUrl + '/shoppingcart/shoppingCartSaveController/editShoppingCartMoreDelFalg',
                    data: aes
                }).then((res) => {
                    // 如果成功，重新加载数据。
                    if (res.data.errorCode === '0000') {
                        this.shopData();
                    }
                    // 取消全选，关闭弹窗
                    this.allSelectActive = false;
                    this.popShow = false;
                })
            })
        },
        // 收藏
        collectFn: function () {
            // 选择的产品id
            let goodsIds = '';
            for (let i = 0, orderLen = this.orderArr.length; i < orderLen; i++) {
                for (let j = 0, listShoppingCartLen = this.orderArr[i].listShoppingCart.length; j < listShoppingCartLen; j++) {
                    if (this.orderArr[i].listShoppingCart[j].productSelectActive) {
                        goodsIds += this.orderArr[i].listShoppingCart[j].goodsId + ',';
                    }
                }
            }
            // 是否有选择商品
            if (goodsIds !== '') {
                let timeStamp = new Date().getTime();
                let params = {'goodsIds': goodsIds.substr(0, goodsIds.length - 1)};
                let md5 = this._cryptojs.MD5(JSON.stringify(params));
                this.$http({
                    method: 'post',
                    url: this._ajaxUrl + '/encryption/aes',
                    headers: {
                        'time-stamp': timeStamp,
                        'data-signature': md5
                    },
                    data: {
                        "timeStamp": timeStamp,
                        "requestBody": JSON.stringify(params)
                    }
                }).then((res) => {
                    let aes = res.data.data.aesRequestBody;
                    this.$http({
                        method: 'post',
                        headers: {
                            "time-stamp": timeStamp,
                            'data-signature': md5
                        },
                        url: this._ajaxUrl + '/goods/collect/batchCollect',
                        data: aes
                    }).then((res) => {
                        // console.log(res.data)
                        if (res.data.errorCode === '0000') {
                            //提示语
                            Toast({
                                message: '收藏成功',
                                position: 'bottom',
                                duration: 3000,
                                className: 'public'
                            });
                        } else {
                            //提示语
                            Toast({
                                message: res.data.message,
                                position: 'bottom',
                                duration: 3000,
                                className: 'public'
                            });
                        }
                    })
                })
            } else {
                //提示语
                Toast({
                    message: '请选择商品',
                    position: 'bottom',
                    duration: 3000,
                    className: 'public'
                });
            }
        }
    }
  }
</script>

<style lang="less" scoped>
  .wrap{
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding-bottom: 198px;
    background: #f6f6f6;
    // 网络异常
    .network-error{
      width: 100%;
      height: 100%;
      padding-top: 480px;
      overflow: hidden;
      text-align: center;
      background: url('../../assets/netword-error.png') center 180px no-repeat;
      background-size: 468px 271px;
      .btn-refresh{
        display: inline-block;
        width: 106px;
        height: 48px;/*px*/
        font-size: 26px;
        text-align: center;
        line-height: 50px;/*px*/
        color: #0066cc;
        /*border: 1px solid #0066cc;*/
        box-shadow: 0 0 0 1px #0066cc;
        border-radius: 10px;
        margin-bottom: 2px;
      }
    }
    // 未登录
    .login-no{
      width: 100%;
      height: 100%;
      padding-top: 480px;
      overflow: hidden;
      text-align: center;
      background: url('../../assets/shop-car/login-no.png') center 160px no-repeat;
      background-size: 355px 292px;
      .btn-login{
        display: inline-block;
        width: 160px;
        height: 48px;
        font-size: 26px;
        text-align: center;
        line-height: 50px;/*px*/
        color: #0066cc;
        /*border: 1px solid #0066cc;*/
        box-shadow: 0 0 0 1px #0066cc;
        border-radius: 10px;
        margin-bottom: 2px;
      }
    }
    // 购物车为空
    .shop-empty{
      width: 100%;
      height: 100%;
      padding-top: 440px;
      overflow: hidden;
      text-align: center;
      background: url('../../assets/shop-car/shop-car-empty.png') center 180px no-repeat;
      background-size: 268px 254px;
      a{
        font-size: 26px;
        line-height: 34px;
        text-decoration: underline;
        color: #0066cc;
      }
    }
    // 购物车内容
    .shop-view{
      width: 100%;
      height: 100%;
      overflow: hidden;
      .shop-ctn{
        width: 100%;
        height: 100%;
        overflow-y: auto;
      }
    }
    // 购物车头部
    .shop-header{
      width: 100%;
      height: 81px;
      padding: 0 30px;
      overflow: hidden;
      font-size: 32px;
      line-height: 80px;
      color: #222;
      background: #fff;
      /*border-bottom: 1px solid #eeeeee;*/
      box-shadow: 0 -1px #eee inset;
      .header-text{
        float: left;
      }
      .btn{
        float: right;
      }
    }
    .order{
      width: 100%;
      margin-bottom: 16px;
      // 订单头部
      .order-header{
        width: 100%;
        height: 81px;
        padding-left: 86px;
        padding-right: 30px;
        font-size: 28px;
        text-align: left;
        line-height: 80px;
        color: #666;
        background: #fff5e2 url('../../assets/icon/icon-select.png') 30px center no-repeat;
        background-size: 40px 40px;
        /*border-bottom: 1px solid #eeeeee;*/
        box-shadow: 0 -1px #eee inset;
        input{
          display: none;
        }
        &.active{
          background: #fff5e2 url('../../assets/icon/icon-select-active.png') 30px center no-repeat;
          background-size: 40px 40px;
        }
      }
      // 订单公司
      .order-firm{
        padding-right: 34px;
      }
      // 订单提示
      .order-tip{
        width: 100%;
        min-height: 30px;
        padding: 8px 30px;
        font-size: 22px;
        letter-spacing: -.5px;
        line-height: 30px;
        color: #f82222;
        background: #fff5f5;
      }
      // 订单内容
      .order-ctn{
        width: 100%;
        background: #fff;
        // 订单详情
        .order-detail{
          position: relative;
          width: 100%;
          padding-left: 102px;
          &:last-child .product-detail{
            box-shadow: none;
          }
        }
        // 商品选择
        .product-select{
          position: absolute;
          top: 0;
          left: 0;
          width: 102px;
          height: 100%;
          background: url('../../assets/icon/icon-select.png') center center no-repeat;
          background-size: 40px 40px;
          input{
            display: none;
          }
          &.active{
            background: url('../../assets/icon/icon-select-active.png') center center no-repeat;
            background-size: 40px 40px;
          }
        }
        // 商品详情
        .product-detail{
          display: flex;
          flex-direction: row;
          width: 100%;
          height: 100%;
          padding: 30px 30px 30px 0;
          /*border-bottom: 1px solid #eeeeee;*/
          box-shadow: 0 -1px #eee inset;
        }
        // 商品图片
        .product-img{
          position: relative;
          width: 178px;
          height: 178px;
          /*border: 1px solid #cccccc;*/
          box-shadow: 0 0 0 1px #ccc;
          img{
            position: absolute;
            top: 50%;
            left: 50%;
            max-width: 100%;
            max-height: 100%;
            transform: translate(-50%, -50%);
          }
          // 库存提示
          .warm-tip{
            position: absolute;
            bottom: 0;
            left: 50%;
            height: 50px;
            font-size: 22px;
            line-height: 50px;
            color: #f82222;
            white-space: nowrap;
            transform: translate(-50%, 100%);
          }
        }
        // 商品信息
        .product-infor{
          flex: 1;
          padding-left: 30px;
          .infor-div{
            width: 100%;
            margin-bottom: 4px;
            overflow: hidden;
          }
          // 产品名字
          .product-name{
            float: left;
            width: 210px;
            height: 36px;
            overflow: hidden;
            font-size: 32px;
            white-space: nowrap;
            line-height: 36px;
            text-overflow: ellipsis;
            color: #222222;
          }
          // 产品价格
          .product-price{
            float: right;
            height: 36px;
            font-size: 24px;
            white-space: nowrap;
            line-height: 36px;
            color: #f82222;
            span{
              font-size: 34px;
            }
          }
          // 其他信息
          .infor-other{
            display: block;
            width: 100%;
            li{
              display: block;
              width: 100%;
              min-height: 38px;
              font-size: 22px;
              line-height: 38px;
              text-align: left;
              color: #666;
            }
          }
        }
        // 数量
        .product-amount{
          margin-top: 30px;
          float: right;
          font-size: 22px;
          white-space: nowrap;
          color: #666666;
          .amount-label{
            float: left;
            display: block;
            height: 64px;
            padding-right: 14px;
            line-height: 64px;
          }
          // 数量操作按钮
          .btn-amount{
            float: left;
            display: block;
            width: 65px;
            height: 64px;
            vertical-align: middle;
            text-align: center;
            line-height: 64px;
            /*border: 1px solid #e9e9e9;*/
            box-shadow: 0 0 0 1px #ccc;
            /*margin-right: 1px;*/
            &.amount-reduce{
              color: #d2d2d2;
            }
            &.amount-add{
              color: #f82222;
            }
          }
          // 数量输入框
          .amount-input{
            float: left;
            display: block;
            width: 88px;
            height: 64px;
            vertical-align: middle;
            font-size: 24px;
            text-align: center;
            line-height: 1;
            color: #333;
            border: none;
            /*border-top: 1px solid #e9e9e9;*/
            /*border-bottom: 1px solid #e9e9e9;*/
            box-shadow: 0 1px #ccc,0 -1px #ccc,-1px 0 #ccc;
            border-radius: 0;
            -webkit-appearance: none;
            outline: none;
          }
        }
      }
    }
    // 操作栏
    .shop-opt{
      position: fixed;
      bottom: 98px;
      left: 0;
      width: 100%;
      height: 101px;
      padding-left: 178px;
      background: #fff;
      /*border-top: 1px solid #ececec;*/
      box-shadow: 0 1px #ececec inset;
      // 全选
      .select-all{
        position: absolute;
        top: 0;
        left: 0;
        width: 148px;
        height: 100%;
        padding-left: 86px;
        font-size: 28px;
        white-space: nowrap;
        text-align: left;
        line-height: 100px;
        color: #666;
        background: url('../../assets/icon/icon-select.png') 30px center no-repeat;
        background-size: 40px 40px;
        input{
          display: none;
        }
        &.active{
          background: url('../../assets/icon/icon-select-active.png') 30px center no-repeat;
          background-size: 40px 40px;
        }
      }
      // 去结算
      .go-buy{
        position: relative;
        width: 100%;
        padding-right: 278px;
        // 总价
        .total{
          width: 100%;
          height: 100%;
          font-size: 28px;
          text-align: right;
          line-height: 100px;
          white-space: nowrap;
          color: #666;
          .total-price{
            font-size: 24px;
            color: #f82222;
            span{
              font-size: 34px;
            }
          }
        }
        // 结算按钮
        .btn-buy{
          position: absolute;
          top: 0;
          right: 0;
          width: 248px;
          height: 100%;
          font-size: 34px;
          text-align: center;
          line-height: 100px;
          color: #fff;
          background: #f82222;
        }
      }
      // 去编辑
      .go-edit{
        width: 100%;
        line-height: 100px;
        text-align: right;
        padding-right: 30px;
        .btn{
          display: inline-block;
          height: 60px;
          margin-left: 10px;
          padding: 0 30px;
          vertical-align: middle;
          font-size: 28px;
          line-height: 62px;/*px*/
          border-radius: 60px;
        }
        // 删除
        .btn-del{
          color: #999;
          background: #fff;
          /*border: 1px solid #999999;*/
          box-shadow: 0 0 0 1px #999;
        }
        // 收藏
        .btn-collect{
          color: #fff;
          background: #fa8c35;
          /*border: 1px solid #fa8c35;*/
          box-shadow: 0 0 0 1px #fa8c35;
        }
      }
    }
    // 弹窗过渡动画
    .fade-enter-active, .fade-leave-active {
      transition: opacity .3s ease-in;
    }
    .fade-enter, .fade-leave-to{
      opacity: 0;
    }
    // 弹窗-删除
    .pop-shade{
      position: fixed;
      top: 0;
      left: 0;
      z-index: 9999;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, .3);
      transition: all .2s ease-in;
      // 弹窗主体
      .pop-main{
        position: absolute;
        top: 50%;
        left: 50%;
        width: 540px;
        overflow: hidden;
        background: #fff;
        border-radius: 10px;
        transform: translate(-50%, -80%);
      }
      .pop-body{
        width: 100%;
        padding-top: 100px;
        padding-bottom: 70px;
        font-size: 32px;
        text-align: center;
        color: #666666;
      }
      .pop-footer{
        display: flex;
        flex-direction: row;
        width: 100%;
        height: 90px;
        overflow: hidden;
        font-size: 32px;
        text-align: center;
        /*border-top: 1px solid #ccc;*/
        box-shadow: 0 1px #ccc inset;
        .btn{
          flex: 1;
          line-height: 90px;
          /*border-right: 1px solid #ccc;*/
          box-shadow: -1px 0 #ccc inset;
          &.last-child{
            box-shadow: none;
          }
        }
        .btn-yes{
          color: #0066cc;
        }
        .btn-no{
          color: #666;
        }
      }
    }
  }
</style>


