<template>
    <div class="wrap">
        <!--搜索框-->
        <div ref="searchOut" :class="{'curr':searchBarChange}" class="search-out">
            <div class="search-in">
                <router-link :to="{path:'/storeSearch',query:{title:storeInfo.companyName,storeId:$route.query.storeId}}">
                    <i></i>
                    <input type="search" placeholder="搜索本店商品">
                </router-link>
            </div>
        </div>


        <div class="store-main" ref="storeMain">

            <!--顶部图片区域-->
            <div ref="banner" :class="{'marginBottom':searchBarChange}" class="store-banner">
                <img class="store-bg" :src="storeInfo.bgImg" alt="">
                <div class="store-banner-mask"></div>
                <div class="store-logo"><img :src="storeInfo.storeLogoUrl" alt=""></div>
                <div class="store-info">
                    <p class="store-name">{{storeInfo.companyName}}</p>
                    <p class="store-url">{{storeInfo.subDomainName}}.yaohuiw.com</p>
                </div>
                <div :class="{'collected':isCollect}" class="store-collect-btn" @click="addCollect">
                    <i v-show="!isCollect"></i>{{isCollect?'已收藏':'收藏'}}
                </div>
            </div>

            <!--选项卡-->
            <div ref="storeTab" class="store-tab" v-show="!searchBarChange"
                 :class="{'tabFixed':searchBarChange}">
                <div class="store-tab-item">
                    <div class="store-home" @click="$router.push('/storeDetail?storeId='+storeInfo.storeId)">店铺首页</div>
                </div>
                <div class="store-tab-item tab-active" >
                    <div class="all-goods">全部商品</div>
                </div>
            </div>

            <!--筛选条-->
            <ul ref="filterNav"  :class="{'filter-nav-fixed':searchBarChange}"
                class="filter-nav">
                <li @click="selectTab(nav,0)" :class="{active:nav[0].tap}"><span>综合</span>
                </li>
                <li @click="selectTab(nav,1)" :class="{active:nav[1].tap,iconRe:nav[1].icon}">
                    <span>销量</span>
                    <i></i>
                </li>
                <li @click="selectTab(nav,2)" :class="{active:nav[2].tap,iconRe:nav[2].icon}">
                    <span>价格</span>
                    <i></i>
                </li>
                <li class="filter" @click="popupSlider = true" :class="{active:nav[3].tap}">
                    <span>筛选</span>
                    <i></i>
                </li>
            </ul>

            <!--搜索列表-->
            <ul ref="searchMain" class="search-main">
                <li v-show="listArr.length>0" v-for="(item,index) in listArr" :key="index">
                    <a @click.prevent="goto(item)">
                        <div class="list-left">
                            <img :src="item.goodsImg" alt="">
                        </div>
                        <div class="list-right">
                            <h6>{{item.goodsName}}</h6>
                            <p class="desc">{{item.spec}}</p>
                            <p class="desc">{{item.producer}}</p>
                            <p class="desc">{{item.companyName}}</p>
                            <p class="price"
                               v-if="login&&((item.buyFlag==1&&item.goodsInfoAdded==1&&item.productLotAdded==1)?true:false)">
                                ￥<span>{{goodsPriceInt(item)}}</span>.{{goodsPriceDec(item)}}</p>
                            <p class="price" v-if="(item.goodsInfoAdded==1&&item.productLotAdded==1)?false:true">
                                商品已下架</p>
                            <p class="price"
                               v-if="login&&(item.buyFlag==0)&&(item.goodsInfoAdded==1&&item.productLotAdded==1)">
                                定向供应</p>
                            <p class="price"
                               v-if="!login&&((item.goodsInfoAdded==1&&item.productLotAdded==1)?true:false)">
                                登录可见</p>
                        </div>
                    </a>

                    <!--未登录状态和登陆后商品已上架才显示按钮-->
                    <div class="shopcar" v-show="(item.goodsInfoAdded==1&&item.productLotAdded==1)?true:false"
                         @click="showBuyPanel(item)"></div>
                </li>
                <!--搜索无结果-->
                <div v-show="listArr.length==0" class="search-none"></div>
            </ul>
            <p class="loading" v-show="isLoading">加载中</p>

        </div>

        <!--圆形悬浮的购物车按钮-->
        <shopcarBtn :shopCarNum="shopCarNum"></shopcarBtn>

        <!--购买面板-->
        <div class="buy-panel">
            <mt-popup
                    v-model="popupVisible"
                    position="bottom">
                <div @touchmove.prevent>
                    <div class="title">
                        <span>{{buyPopCtn.companyName}}</span>
                        <i class="close" @click="popupVisible=false"></i>
                    </div>
                    <div class="desc">
                        <div>
                            <p>商品名称</p>
                            <p>{{buyPopCtn.goodsName}}&nbsp;&nbsp;&nbsp;{{buyPopCtn.spec}}</p>
                        </div>
                        <div>
                            <p>生产厂家</p>
                            <p>{{buyPopCtn.producer}}</p>
                        </div>
                        <div>
                            <p>批准文号</p>
                            <p>{{buyPopCtn.approvalNumber}}</p>
                        </div>
                        <div>
                            <p>件装量</p>
                            <p>{{buyPopCtn.pieceLoading}}{{buyPopCtn.goodsDeno?buyPopCtn.goodsDeno:'盒'}}/件</p>
                        </div>
                    </div>

                    <!--购买选项-->
                    <!--<buyOption :buyType="buyType" :batch="batch" :goodsDeno="goodsDeno"></buyOption>-->
                    <!--<buyOption :buyPopCtn="buyPopCtn" :unit="unit"></buyOption>-->
                    <div class="option">
                        <div class="section">
                            <div class="left"><span>采购方式:</span></div>
                            <div class="right">
                        <span v-for="(item,index) in buyPopCtn.buyType" :key="index"
                              @click="optionCheck(buyPopCtn.buyType,item)"
                              :class="{'option-check':item.check}">{{item.name}}<i
                                v-show="item.check"></i></span>
                            </div>
                        </div>
                        <div class="section">
                            <div class="left left-center"><span>批次:</span></div>
                            <div class="right right-center">
                                <!--<span v-for="(item,index) in batch" :key="index" @click="optionCheck(batch,item)"-->
                                <!--:class="{'option-check':item.check}">{{item.name}}<i-->
                                <!--v-show="item.check"></i></span>-->
                                <span class="option-check">{{buyPopCtn.goodsProductLotCode}}<i v-show="true"></i></span>
                                <p class="limit-date">(生产日期:2016-10-31
                                    有效期至{{prodEndDate(buyPopCtn.goodsProductLotEndDate)}})</p>

                            </div>
                        </div>

                        <div class="section">
                            <div class="left left-bottom"><span>采购数量:</span></div>
                            <div class="right">
                                <div class="num-btn">
                                    <span class="sub" @click="subNum">-</span>
                                    <input class="num" type="tel" v-model="buyNum">
                                    <span class="add" @click="addNum">+</span>
                                </div>
                                <span class="box">{{buyPopCtn.unit}}</span>
                                <p class="stock">当前库存量:{{buyPopCtn.stock}}{{buyPopCtn.unit}}<span class="stock-desc">{{minimum}}{{buyPopCtn.unit}}起</span>
                                </p>
                            </div>
                        </div>
                    </div>

                    <div class="footer">
                        <div class="footer-left">
                            <router-link :to="{path:'/storeDetail',query:{storeId:buyPopCtn.storeId}}" class="store">
                                <div>
                                    <i></i>
                                    <p>进入店铺</p>
                                </div>
                            </router-link>
                            <router-link :to="login?'/shopCar':'/login'" class="shop-car">
                                <div>
                                    <i><span ref="badge" class="badge"
                                             :class="{'animate':animate}">{{shopCarNum}}</span></i>
                                    <p>购物车</p>
                                </div>
                            </router-link>
                        </div>
                        <div class="footer-right">
                            <div class="add-shop-car" :class="{'forbid-buy':buyFlag==0&&login}"
                                 @click="login?addShopcar(0):$router.push('/login')">加入购物车
                            </div>
                            <a :class="{'forbid-buy':buyFlag==0&&login}" @click.prevent="buyNow()" class="buy-now">立即采购</a>
                        </div>
                    </div>
                </div>
            </mt-popup>
        </div>

        <!--筛选面板-->
        <div class="filter-out">
            <mt-popup
                    v-model="popupSlider"
                    position="right">
                <!--筛选面板-->
                <div ref="filterPanel" @touchmove.prevent v-show="!allBrandListShow" class="filter-panel">

                    <div>
                        <p>品牌</p>
                        <ul>
                            <li @click="listCheck(item)" :class="{'filter-brand-select':item.check}"
                                class="filter-brand" v-for="(item,index) in filterPanelArr.brandList">
                                <span>{{item.brandName}}</span>
                            </li>
                            <li @click="allBrandList('品牌')" class="filter-brand all-brand">全部品牌<i></i></li>
                        </ul>
                    </div>
                    <div>
                        <p>剂型</p>
                        <ul>
                            <li @click="listCheck(item)" :class="{'filter-brand-select':item.check}"
                                class="filter-brand" v-for="(item,index) in filterPanelArr.agentTypeList">
                                <span>{{item.agentTypeName}}</span>
                            </li>
                            <li @click="allBrandList('剂型')" class="filter-brand all-brand">全部剂型<i></i></li>
                        </ul>
                    </div>
                </div>

                <!--全部品牌列表的头部-->
                <div v-show="allBrandListShow" class="index-list-header">
                    <span @click="back" class="back"><i></i>返回</span>
                    <span class="title">{{filterType}}</span>
                    <span @click="confirm" class="confirm">确定</span>
                </div>

                <!--全部品牌列表-->
                <mt-index-list v-show="allBrandListShow&&filterType=='品牌'">
                    <mt-index-section v-for="(item,index) in secondBrandArr" :index="item[0].id" :key="index">
                        <div class="brand-item" @click="listCheck(list)" v-for="(list,$index) in item"
                             v-if="list.brandName" :key="$index">
                            <mt-cell :title="list.brandName">
                                <i v-show="list.check"></i>
                            </mt-cell>
                        </div>
                    </mt-index-section>
                </mt-index-list>

                <!--全部剂型列表-->
                <mt-index-list v-show="allBrandListShow&&filterType=='剂型'">
                    <mt-index-section v-for="(item,index) in secondAgentArr" :index="item[0].id" :key="index">
                        <div class="brand-item" @click="listCheck(list)" v-for="(list,$index) in item"
                             v-if="list.agentTypeName" :key="$index">
                            <mt-cell :title="list.agentTypeName">
                                <i v-show="list.check"></i>
                            </mt-cell>
                        </div>
                    </mt-index-section>
                </mt-index-list>
            </mt-popup>
        </div>


        <!--取消收藏弹窗-->
        <div class="cancel-collect">
            <mt-popup
                    v-model="popupCollect"
                    position="bottom">
                <div class="cancel" @click="cancelCollect">取消收藏</div>
                <div class="wait" @click="popupCollect=false">容我想想</div>
            </mt-popup>
        </div>

    </div>
</template>

<script>
    import shopcarBtn from '../subcom/shopcar-btn';
    import {Toast} from 'mint-ui';
    import Vue from 'vue';

    export default {
        name: "store-prod-list",
        components: {
            shopcarBtn,
        },
        data() {
            return {
                couponId:'',
                //首字母对应的品牌
                secondBrandArr: [],
                //首字母对应的剂型
                secondAgentArr: [],
                //一级品牌筛选选中的内容
                firstBrand: '',
                //一级剂型筛选选中的内容
                firstAgent: '',
                //搜索列表总页数
                totalPages: null,
                //搜索数据的请求体
                searchParams:null,
                //加载中
                isLoading: false,
                //数据全部加载完毕
                isEnd: false,
                //账号认证状态
                numberTip:0,
                //定向供应0 可以购买1
                buyFlag: 0,
                //购买弹窗信息
                buyPopCtn:{},
                //最小起订量
                minimum:0,
                //购买数量输入框
                buyNum:0,
                //店铺信息
                storeInfo: {},
                scrollTop: 0,
                //取消收藏弹窗是否显示
                popupCollect: false,
                //是否收藏
                isCollect: false,
                //筛选是否选中
                filterCheck: [],
                //筛选面板第一页数据
                filterPanelArr: {},
                //筛选面板第二页是否显示
                allBrandListShow: false,
                //筛选类型
                filterType: '',
                token: '',
                //筛选条选中状态
                nav: [
                    {tap: true, icon: false},
                    {tap: false, icon: false},
                    {tap: false, icon: false},
                    {tap: false, icon: false}
                ],
                //搜索框是否吸顶效果
                searchBarChange: false,
                //全部商品显示的列表
                listArr: [],
                //是否登录
                login: document.cookie.split('token=')[1] ? true : false,
                //购物车按钮的数字
                shopCarNum: 0,
                //筛选面板的是否显示
                popupSlider: false,
                //购买面板是否显示
                popupVisible: false,
                //采购类型
                buyType: [{name: '拆零', check: true}, {name: '整件', check: false}],
                //批次
                batch: [{name: '20170506', check: true}, {name: '20170506', check: false}, {
                    name: '20170506',
                    check: false
                }, {name: '20170506', check: false}, {name: '20170506', check: false},],
                //加入购物车显示的动画
                animate: false,
            }
        },
        methods: {
            //购买面板点击选项
            optionCheck(type, item) {
                if (item.check) {
                    return
                }
                if (item.name == '拆零') {
                    this.minimum = this.buyPopCtn.openMinmum?this.buyPopCtn.openMinmum:Math.ceil(this.buyPopCtn.openMinmumPrice/this.buyPopCtn.openMoney);
                    this.buyNum = this.minimum;
                    this.buyPopCtn.unit = this.buyPopCtn.goodsDeno?this.buyPopCtn.goodsDeno:'盒';
                    this.buyPopCtn.stock = this.buyPopCtn.goodsProductLotWareStock
                } else {
                    this.minimum = this.buyPopCtn.completeMinmum?this.buyPopCtn.completeMinmum:Math.ceil(this.buyPopCtn.completeMinmumPrice/this.buyPopCtn.completeMoney);
                    this.buyNum = this.minimum;
                    this.buyPopCtn.unit = '件';
                    this.buyPopCtn.stock = Math.floor(this.buyPopCtn.goodsProductLotWareStock / this.buyPopCtn.pieceLoading);
                }
                //改变样式
                for (let i = 0; i < type.length; i++) {
                    type[i].check = false;
                }
                item.check = true;
            },
            //立即采购
            buyNow() {
                if (this.login) {
                    //已登录
                    if (this.buyFlag == 1) {
                        //可以购买
                        this.addShopcar(1)
                    } else {
                        //不可以购买
                    }
                } else {
                    //未登录就跳去登录
                    this.$router.push('/login')
                }
            },
            //商品上架跳转
            goto(item) {
                if ((item.goodsInfoAdded == 1 && item.productLotAdded == 1)) {
                    //商品已上架可以跳转到详情页
                    this.$router.push({path: '/goodsDetail', query: {goodsId: item.goodsId}})
                } else {
                    //商品已下架不可以跳转
                }
            },
            subNum() {
                if (this.buyNum <= 0) {
                    return;
                }
                this.buyNum--;
            },
            addNum() {
                this.buyNum++;
            },
            //获取产品有效期
            prodEndDate(timeStamp) {
                let date = new Date(timeStamp);
                let Y = date.getFullYear() + '-';
                let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
                let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
                return Y + M + D
            },
            //全部商品显示的价格
            goodsPriceInt(item) {
                if (item.goodsSaleChanne == 1) {
                    return item.completeMoney.toString().split('.')[0];
                } else if (item.goodsSaleChanne == 2 || item.goodsSaleChanne == 3) {
                    return item.openMoney.toString().split('.')[0];
                }
            },
            //全部商品显示的价格
            goodsPriceDec(item) {
                if (item.goodsSaleChanne == 1) {
                    if (item.completeMoney.toString().indexOf('.') == -1) {
                        return '00';
                    } else {
                        return item.completeMoney.toString().split('.')[1].length==1?item.completeMoney.toString().split('.')[1]+'0':item.completeMoney.toString().split('.')[1];
                    }
                } else if (item.goodsSaleChanne == 2 || item.goodsSaleChanne == 3) {
                    if (item.openMoney.toString().indexOf('.') == -1) {
                        return '00';
                    } else {
                        return item.openMoney.toString().split('.')[1].length==1?item.openMoney.toString().split('.')[1]+'0':item.openMoney.toString().split('.')[1];
                    }
                }
            },
            //取消收藏
            cancelCollect() {
                //取消收藏请求
                let timeStamp = new Date().getTime();
                let params = {"storeId": this.$route.query.storeId};
                let md5 = this._cryptojs.MD5(JSON.stringify(params));
                this.$http({
                    method: 'post',
                    url: this._ajaxUrl + '/encryption/aes',
                    headers: {
                        'data-signature': md5,
                        'accessToken': this.token
                    },
                    data: {
                        "timeStamp": timeStamp,
                        "requestBody": JSON.stringify(params)
                    }
                }).then((res) => {
                        let aes = res.data.data.aesRequestBody;
                        this.$http({
                            method: 'post',
                            headers: {
                                'data-signature': md5,
                                accessToken: this.token,
                                'time-stamp':timeStamp
                            },
                            url: this._ajaxUrl + '/store/collect/remove',
                            data: aes
                        }).then((res) => {
                            console.log(res.data);
                            if (res.data.errorCode == '0000') {
                                this.isCollect = false;
                                this.popupCollect = false;
                            } else if (res.data.errorCode == 'login_0004') {
                                //登录超时 清空cookie
                                alert('登录超时');
                                document.cookie = 'token=';
                                this.login = false;
                            }
                        })
                    }
                );
            },
            //加入收藏
            addCollect() {
                if (this.isCollect) {
                    this.popupCollect = true;
                    return;
                }

                //添加收藏请求
                let timeStamp = new Date().getTime();
                let params = {"storeId": this.$route.query.storeId};
                let md5 = this._cryptojs.MD5(JSON.stringify(params));
                this.$http({
                    method: 'post',
                    url: this._ajaxUrl + '/encryption/aes',
                    headers: {
                        'data-signature': md5,
                        'accessToken': this.token
                    },
                    data: {
                        "timeStamp": timeStamp,
                        "requestBody": JSON.stringify(params)
                    }
                }).then((res) => {
                        let aes = res.data.data.aesRequestBody;
                        this.$http({
                            method: 'post',
                            headers: {
                                'data-signature': md5,
                                accessToken: this.token,
                                'time-stamp':timeStamp
                            },
                            url: this._ajaxUrl + '/store/collect/collectStore',
                            data: aes
                        }).then((res) => {
                            console.log(res.data);
                            if (res.data.errorCode == '0000') {
                                this.isCollect = true;
                            } else if (res.data.errorCode == 'login_0004') {
                                //登录超时 清空cookie
                                alert('未登录');
                                document.cookie = 'token=';
                                this.login = false;
                            }
                        })
                    }
                );
            },
            //页面滚动事件
            storeScroll() {
                if(this.listArr.length==0){
                    return
                }
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
                if (document.body.className == 'forbid-scroll') {
                    return;
                }

                this.searchBarChange = (scrollTop >= (this.$refs.banner.offsetHeight - this.$refs.searchOut.offsetHeight
                )) ? true : false

            },
            //加载更多
            loadmore() {
                if(this.listArr.length==0){
                    return
                }
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
                this.bottomDistance = this.$refs.searchMain.offsetHeight - document.body.offsetHeight;
                if (scrollTop >= this.bottomDistance+350) {
                    if (this.isLoading || this.isEnd) return;
                    this.isLoading = true;
                    this.searchParams.pageNo++;
                    if (this.searchParams.pageNo >= this.totalPages) {
                        this.isEnd = true;
                    }

                    console.log(this.searchParams);
                    let timeStamp = new Date().getTime();
                    let md5 = this._cryptojs.MD5(JSON.stringify(this.searchParams));
                    this.$http({
                        method: 'post',
                        url: this._ajaxUrl + '/encryption/aes',
                        headers: {
                            'data-signature': md5,
                            accessToken: this.token
                        },
                        data: {
                            "timeStamp": timeStamp,
                            "requestBody": JSON.stringify(this.searchParams)
                        }
                    }).then((res) => {
                            let aes = res.data.data.aesRequestBody;
                            this.$http({
                                method: 'post',
                                headers: {
                                    'data-signature': md5,
                                    accessToken: this.token,
                                    'time-stamp':timeStamp
                                },
                                url: this._ajaxUrl + '/index/searchGoods',
                                data: aes
                            }).then((res) => {
                                if (res.data.errorCode == '0000') {
                                    console.log(res.data.data);

                                    //加载成功
                                    this.isLoading = false;
                                    this.listArr = this.listArr.concat(res.data.data.goodsItemList);
                                }
                            })
                        }
                    )
                }
            },
            //显示购买面板
            showBuyPanel(item) {
                console.log(item);
                this.popupVisible = true;
                this.buyPopCtn = item;
                this.buyFlag = item.buyFlag;
                if (item.goodsSaleChanne == 1) {
                    //最小起订量
                    this.minimum = this.buyPopCtn.completeMinmum?this.buyPopCtn.completeMinmum:Math.ceil(this.buyPopCtn.completeMinmumPrice/this.buyPopCtn.completeMoney);
                    //采购数量输入框默认显示的数量
                    this.buyNum = this.minimum;
                    //拆零整件选项
                    Vue.set(this.buyPopCtn, 'buyType', [{name: '整件', check: true}]);
                    //结算单位
                    Vue.set(this.buyPopCtn, 'unit', '件');
                    //库存量
                    Vue.set(this.buyPopCtn, 'stock', Math.floor(item.goodsProductLotWareStock / item.pieceLoading));
                } else {
                    //最小起订量
                    this.minimum = this.buyPopCtn.openMinmum?this.buyPopCtn.openMinmum:Math.ceil(this.buyPopCtn.openMinmumPrice/this.buyPopCtn.openMoney);
                    //采购数量输入框默认显示的数量
                    this.buyNum = this.minimum;
                    //结算单位
                    Vue.set(this.buyPopCtn, 'unit', item.goodsDeno?item.goodsDeno:'盒');
                    //库存量
                    Vue.set(this.buyPopCtn, 'stock', item.goodsProductLotWareStock);
                    if(item.goodsSaleChanne == 2){
                        //拆零整件选项
                        Vue.set(this.buyPopCtn, 'buyType', [{name: '拆零', check: true}]);
                    }else{
                        //拆零整件选项
                        Vue.set(this.buyPopCtn, 'buyType', [{name: '拆零', check: true}, {name: '整件', check: false}]);
                    }
                }
            },
            //添加购物车
            addShopcar(num) {
                if (this.animate) {
                    //没做完动画不执行代码
                    return
                }
                let type;
                for (let i = 0; i < this.buyPopCtn.buyType.length; i++) {
                    if (this.buyPopCtn.buyType[i].check) {
                        if (this.buyPopCtn.buyType[i].name == '拆零') {
                            type = 'open'
                        } else {
                            type = 'complete'
                        }
                    }
                }
                let params = {
                    goodsId: this.buyPopCtn.goodsId,
                    goodsNum: this.buyNum,
                    goodsProductLotCode: this.buyPopCtn.goodsProductLotCode,
                    shoppingCartType: num,
                    purchase: type
                };
                this.token = document.cookie.split('token=')[1] ? document.cookie.split('token=')[1] : '';
                let timeStamp = new Date().getTime();
                let md5 = this._cryptojs.MD5(JSON.stringify(params));
                this.$http({
                    method: 'post',
                    url: this._ajaxUrl + '/encryption/aes',
                    headers: {
                        'data-signature': md5,
                        accessToken: this.token
                    },
                    data: {
                        "timeStamp": timeStamp,
                        "requestBody": JSON.stringify(params)
                    }
                }).then((res) => {
                        let aes = res.data.data.aesRequestBody;
                        this.$http({
                            method: 'post',
                            headers: {
                                'data-signature': md5,
                                accessToken: this.token,
                                'time-stamp':timeStamp
                            },
                            url: this._ajaxUrl + '/shoppingcart/shoppingCartSaveController/saveShoppingCartGoodsInfo',
                            data: aes
                        }).then((res) => {
                            console.log(res.data);
                            if (res.data.errorCode == 'login_0004') {
                                //登录超时 清空cookie
                                alert('登录超时');
                                document.cookie = 'token=';
                                this.login = false;
                                return
                            }
                            if (res.data.errorCode == 'store_0001') {
                                //定向供应
                                return
                            }
                            if(res.data.data.numberTip==100){
                                if(num==0){
                                    //加入购物车
                                    //获取购物车数量
                                    let timeStamp = new Date().getTime();
                                    this.$http({
                                        method: 'post',
                                        headers: {
                                            accessToken: this.token,
                                            'time-stamp':timeStamp
                                        },
                                        url: this._ajaxUrl + '/shoppingcart/shoppingCartSaveController/myShoppingCartNumber'
                                    }).then((res) => {
                                        if (res.data.errorCode == 'login_0004') {
                                            this.login = false;
                                            //登录超时 清空cookie
                                            // alert('登录超时');
                                            document.cookie = 'token='
                                        }else{
                                            this.shopCarNum = res.data;
                                            Toast({
                                                message: '添加成功',
                                                position: 'bottom',
                                                duration: 3000,
                                                className: 'add-shop-car'
                                            });
                                            //做动画
                                            this.animate = true;
                                        }
                                    });
                                }else{
                                    //立即购买
                                    //获取购物车信息
                                    this.$router.push({
                                        path: '/orderSubmit',
                                        query: {
                                            'shoppingIds': res.data.data.objectBean.shoppingCartId
                                        }
                                    });

                                }

                            }
                        })
                    }
                );
            },
            //切换筛选条样式
            selectTab(item,index) {
                let sortFlag;
                if (item[index].icon) {
                    item[index].icon = !item[index].icon;
                } else {
                    this.nav.forEach(function (value) {
                        value.tap = false;
                        value.icon = false;
                    });
                    item[index].tap = true;
                    item[index].icon = !item[index].icon;
                }
                //排序标识
                if (index == 0) {
                    sortFlag = 1;
                } else if (index == 1) {
                    sortFlag = item[index].icon ? 2 : 3;
                } else {
                    sortFlag = item[index].icon ? 4 : 5;
                }
                //获取店铺商品列表信息
                this.searchParams = {
                    sortFlag: sortFlag,
                    agentType: this.firstAgent,
                    brandName: this.firstBrand,
                    storeId: this.storeInfo.storeId,
                    couponId:this.couponId,
                    pageNo: 1
                };
                this._ajaxFn(this.searchParams);
            },
            //全部品牌点击确定
            confirm() {
                this.nav[0].tap = true;
                this.nav[1].tap = false;
                this.nav[1].icon = false;
                this.nav[2].tap = false;
                this.nav[2].icon = false;
                this.nav[3].tap = true;
                this.popupSlider = false;
                // this.$emit('submitSearch', false);
                // this.searchNavShow = true;
                // this.content = '';
            },
            //点击全部品牌或剂型
            allBrandList(filterType) {
                this.filterType = filterType;
                this.allBrandListShow = true;
                //有数据只请求一次
                if (this.filterType == '品牌' && this.secondBrandArr.length > 0) {
                    return;
                } else if (this.filterType == '剂型' && this.secondAgentArr.length > 0) {
                    return;
                }

                let url = filterType == '品牌' ? '/index/selectGoodsBrandAllInfo' : '/index/selectGoodsAgentTypeAllInfo';
                let timeStamp = new Date().getTime();
                this.$http({
                    method: 'post',
                    headers: {
                        accessToken: this.token,
                        'time-stamp':timeStamp
                    },
                    url: this._ajaxUrl + url
                }).then((res) => {
                    if (res.data.errorCode == '0000') {
                        if (filterType == '品牌') {
                            this.secondBrandArr = res.data.data;
                        } else {
                            this.secondAgentArr = res.data.data;
                        }
                    }
                    //搜索到数据
                    console.log(res.data.data);
                })
            },
            //点击返回按钮
            back() {
                this.allBrandListShow = false;
            },
            //选中品牌剂型的某个条目
            listCheck(item) {
                Vue.set(item, 'check', !item.check);
                if (item.brandName) {
                    if (item.check) {
                        this.firstBrand += item.brandName + ','
                    } else {
                        this.firstBrand = this.firstBrand.split(item.brandName + ',').join('')
                    }
                } else if (item.agentTypeName) {
                    if (item.check) {
                        this.firstAgent += item.agentTypeName + ','
                    } else {
                        this.firstAgent = this.firstAgent.split(item.agentTypeName + ',').join('')
                    }
                }
            },
            _ajaxFn(data) {
                console.log(data);
                this.isEnd = false;
                let timeStamp = new Date().getTime();
                this.token = document.cookie.split('token=')[1] ? document.cookie.split('token=')[1] : '';
                let md5 = this._cryptojs.MD5(JSON.stringify(data));
                this.$http({
                    method: 'post',
                    url: this._ajaxUrl + '/encryption/aes',
                    headers: {
                        'data-signature': md5,
                        accessToken: this.token
                    },
                    data: {
                        "timeStamp": timeStamp,
                        "requestBody": JSON.stringify(data)
                    }
                }).then((res) => {
                        let aes = res.data.data.aesRequestBody;

                        this.$http({
                            method: 'post',
                            headers: {
                                'data-signature': md5,
                                accessToken: this.token,
                                'time-stamp':timeStamp
                            },
                            url: this._ajaxUrl + '/index/searchGoods',
                            data: aes
                        }).then((res) => {
                            console.log(res.data);
                            this.listArr = res.data.data.goodsItemList;
                            this.totalPages = res.data.data.totalPages;
                            //如果总页数少于等于1页,则不能加载更多
                            if (this.totalPages <= 1) {
                                this.isEnd = true;
                            }
                        })
                    }
                );
            },
        }
        ,
        created() {
            //店铺信息
            this.couponId = this.$route.query.couponId?this.$route.query.couponId:''
            this.token = document.cookie.split('token=')[1] ? document.cookie.split('token=')[1] : '';
            let timeStamp = new Date().getTime();
            this.$http({
                method: 'post',
                headers: {
                    accessToken: this.token,
                    'time-stamp':timeStamp
                },
                url: this._ajaxUrl + '/store/index/' + this.$route.query.storeId,
            }).then((res) => {
                console.log(res.data);
                if (res.data.errorCode == '0000') {
                    //有店铺
                    this.storeInfo = res.data.data.storeInfo;
                    this.isCollect = res.data.data.isCollection;

                    //获取店铺商品列表信息
                    this.searchParams = {
                        sortFlag: 1,
                        agentType: '',
                        brandName: '',
                        storeId:this.storeInfo.storeId,
                        pageNo: 1,
                        couponId:this.couponId
                    };
                    this._ajaxFn(this.searchParams);
                } else {
                    //店铺不存在
                    // alert('店铺不存在')
                }
            });

            //获取购物车数量
            this.$http({
                method: 'post',
                headers: {
                    accessToken: this.token,
                    'time-stamp': timeStamp
                },
                url: this._ajaxUrl + '/shoppingcart/shoppingCartSaveController/myShoppingCartNumber'
            }).then((res) => {
                console.log(res.data);
                if (res.data.errorCode == 'login_0004') {
                    this.login = false;
                    //登录超时 清空cookie
                    // alert('登录超时');
                    document.cookie = 'token='
                }else{
                    this.shopCarNum = res.data
                }
            })
        }
        ,
        mounted() {
            //去掉商品详情使用优惠券跳转过来时带的禁止滚动类名
            document.body.className = '';
            //监听页面滚动
            window.addEventListener('scroll', this.storeScroll);
            //滚动加载更多
            window.addEventListener('touchmove', this.loadmore);

            //监听从店铺优惠券或商品优惠券的路由跳转需要显示全部商品
            // if (this.$route.query.allGoods) {
            //     this.storeTabToggle(this.storeTab[1])
            // }

            //监听购物车圆点的动画结束
            let _this = this;
            this.$refs.badge.addEventListener("webkitAnimationEnd", function () {
                _this.animate = false;
            })
            this.$refs.filterPanel.style.height = document.body.offsetHeight + 'px';
        }
        ,
        //切换路由时注销滚动事件
        destroyed() {
            window.removeEventListener('scroll', this.storeScroll);
            window.removeEventListener('touchmove', this.loadmore);
        }
        ,
        watch: {
            popupSlider(newV, oldV) {
                if (!newV) {
                    //关闭筛选面板
                    this.allBrandListShow = false;
                        let params = {
                            sortFlag:1,
                            pageNo:1,
                            storeId:this.storeInfo.storeId,
                            agentType: this.firstAgent,
                            brandName: this.firstBrand,
                            couponId: this.couponId
                        };
                        this._ajaxFn(params);
                        this.nav[0].tap = true;
                        this.nav[1].tap = false;
                        this.nav[1].icon = false;
                        this.nav[2].tap = false;
                        this.nav[2].icon = false;
                        this.nav[3].tap = true;

                }else{
                    //一级筛选项只请求一次
                    if (this.filterPanelArr.brandList) {
                        return;
                    }
                    //展开一级筛选面板请求数据
                    let timeStamp = new Date().getTime();
                    this.token = document.cookie.split('token=')[1] ? document.cookie.split('token=')[1] : '';
                    this.$http({
                        method: 'post',
                        headers: {
                            accessToken: this.token,
                            'time-stamp':timeStamp
                        },
                        url: this._ajaxUrl + '/index/selectGoodsBrandAndAgentType',
                    }).then((res) => {
                        console.log(res.data.data);
                        if (res.data.errorCode == '0000') {
                            this.filterPanelArr = res.data.data
                        }
                    })
                }
            }
            ,
            allBrandListShow(newV) {
                if (newV) {
                    this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
                    document.body.className = 'forbid-scroll';
                    this.$refs.storeMain.style.transform = 'translateY(' + (-this.scrollTop) + 'px)'

                    if (this.searchBarChange) {
                        this.searchBarChange = true;
                        this.$refs.filterNav.style.top = this.$refs.searchOut.offsetHeight + this.scrollTop + 'px'
                    }

                } else {
                    document.body.className = '';
                    this.$refs.storeMain.style.transform = '';
                    if (this.searchBarChange) {
                        this.$refs.filterNav.style.top = ''
                    }
                    window.scrollTo(0, this.scrollTop);
                }
            }
            ,
            popupVisible(newV) {
                if (newV) {
                    this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
                    document.body.className = 'forbid-scroll';
                    this.$refs.storeMain.style.transform = 'translateY(' + (-this.scrollTop) + 'px)'

                    if (this.searchBarChange) {
                        this.searchBarChange = true;
                        this.$refs.filterNav.style.top = this.$refs.searchOut.offsetHeight + this.scrollTop + 'px'
                    }

                } else {
                    document.body.className = '';
                    this.$refs.storeMain.style.transform = '';
                    if (this.searchBarChange) {
                        this.$refs.filterNav.style.top = ''
                    }
                    window.scrollTo(0, this.scrollTop);
                }
            },
            buyNum(newV) {
                if (newV-0 <= this.minimum-0) {
                    this.buyNum = this.minimum
                } else if (newV-0 >= this.buyPopCtn.stock-0) {
                    this.buyNum = this.buyPopCtn.stock
                }
            }

        }
    }
</script>

