                align-items: center;
                justify-content: space-between;
                box-shadow: 0 -1px #ddd inset;
                .back {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    line-height: 86px; /*px*/
                    font-size: 26px;
                    color: #666;
                    i {
                        display: inline-block;
                        width: 13px;
                        height: 22px;
                        background: url("../../assets/drop-arrow-right.png");
                        background-size: cover;
                        transform: rotateZ(180deg);
                        margin-right: 10px;
                    }
                }
                .title {
                    font-size: 32px;
                    color: #333;
                }
                .confirm {

                    font-size: 26px;
                    color: #666;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
            /*全部品牌/剂型*/
            .mint-indexlist {
                .mint-indexlist-content {
                    .mint-indexsection {
                        .brand-item {
                            box-shadow: 0 -1px #ddd inset;
                        }
                        .mint-cell {
                            height: 88px;
                            i {
                                width: 23px;
                                height: 17px;
                                display: inline-block;
                                background: url("../../assets/filter-check.png");
                                background-size: cover;
                            }
                        }
                    }
                }
            }
        }
    }

    /*购买面板*/
    .buy-panel {
        .mint-popup {
            width: 100%;
            background: #fff;

            .title {
                height: 80px;
                padding: 0 30px;
                box-shadow: 0 -1px 0 0 #ddd inset;
                display: flex;
                justify-content: space-between;
                align-items: center;
                line-height: 82px; /*px*/
                span {
                    font-size: 28px;
                    color: #222;
                }
                .close {
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    background: url("../../assets/close.png");
                    background-size: cover;
                }
            }
            .desc {
                box-shadow: 0 -1px 0 0 #ddd inset;
                margin-bottom: 2px;
                padding: 15px 30px;

                div {
                    display: flex;
                    text-align: left;
                    color: #666;
                    font-size: 26px;
                    line-height: 26px;
                    margin-bottom: 16px;
                    p:first-child {
                        flex: 1;
                    }
                    p:last-child {
                        flex: 4;
                        word-break: break-all;
                    }
                }
                div:last-child {
                    margin-bottom: 0;
                }
            }
            .option {
                padding: 22px 30px;
                box-shadow: 0 -1px #e5e5e5 inset;
                background: #fff;
                .limit-date {
                    color: #0066cc;
                    font-size: 20px;
                }

                .section {
                    overflow: hidden;

                    margin-bottom: 24px;
                    .left {
                        float: left;
                        width: 138px;
                        text-align: left;
                        height: 100%;
                        display: inline-block;
                        font-size: 26px;
                        color: #666;
                        line-height: 55px; /*px*/

                    }
                    .left.left-center {
                        text-indent: 52px;
                    }
                    .left.left-bottom {
                        line-height: 64.5px;
                        margin: 0;

                    }
                    .right {
                        float: left;
                        width: 552px;
                        display: inline-block;
                        padding-bottom: 1px;
                        margin-top: 2px;
                        margin-bottom: 2px;
                        span {
                            position: relative;
                            display: inline-block;
                            font-size: 24px;
                            color: #222;
                            box-shadow: 0 0 0 2px #e9e9e9;
                            padding: 14px 18px;
                            margin-right: 24px;

                            i {
                                position: absolute;
                                bottom: 0;
                                right: 0;
                                width: 24px;
                                height: 24px;
                                background: url("../../assets/option-check.png") no-repeat;
                                background-size: cover;
                            }
                        }
                        /*选项选中样式*/
                        span.option-check {
                            box-shadow: 0 0 0 3px #fa8c35;
                        }
                        p.stock {
                            font-size: 20px;
                            color: #999;
                            padding: 0;
                            margin-top: 16px;
                            text-align: left;
                            .stock-desc {
                                box-shadow: none;
                                font-size: 20px;
                                color: #999;
                                margin-left: 10px;
                                padding: 0;
                                margin-right: 0;
                            }
                        }
                        .num-btn {
                            float: left;
                            height: 64px;
                            display: flex;
                            box-shadow: 0 0 0 1px #ccc;
                            span {
                                width: 64px;
                                margin: 0;
                                padding: 0;
                                text-align: center;
                                box-shadow: none;
                                line-height: 64px; /*px*/
                            }
                            input.num {
                                outline: none;
                                text-align: center;
                                width: 90px;
                                border: none;
                                border-radius: 0;
                                padding: 1px 0;
                                font-size: 24px;
                                color: #333;
                            }
                            span.sub {
                                box-shadow: 1px 0 0 0 #ccc;
                                font-size: 30px;
                                color: #d2d2d2;
                            }
                            span.add {
                                box-shadow: -1px 0 0 0 #ccc;
                                font-size: 30px;
                                color: #f82222;
                            }
                        }
                        .box {
                            border: 1px solid transparent;
                            padding: 0;
                            line-height: 64px; /*px*/
                            margin-left: 16px;
                            margin-right: 24px;
                            color: #666;
                            font-size: 26px;
                            box-shadow: none;
                        }
                    }
                    .right.right-center {
                        margin-top: 3px;
                        span {
                            margin-bottom: 24px;
                        }
                    }
                }
                .section:nth-last-child(1) {
                    margin-bottom: 0;
                }
            }
            .footer {
                height: 100px;
                display: flex;
                .footer-left {
                    flex: 0.34;
                    height: 100%;
                    /*width: 256px;!*px*!*/
                    display: flex;
                    p {
                        color: #222;
                        font-size: 20px;
                    }
                    .store {
                        flex: 1;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        div {
                            text-align: center;
                        }
                        i {
                            width: 44px;
                            height: 44px;
                            display: inline-block;
                            background: url("../../assets/tabbar-home-normal.png");
                            background-size: cover;
                        }

                    }
                    .shop-car {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        flex: 1;
                        div {
                            text-align: center;
                        }
                        i {
                            width: 44px;
                            height: 44px;
                            display: inline-block;
                            background: url("../../assets/tabbar-shopcar-normal.png");
                            background-size: cover;
                            position: relative;
                            .badge {
                                position: absolute;
                                right: -10px;
                                top: -8px;
                                width: 30px;
                                height: 30px;
                                border-radius: 50%;
                                border: 1px solid transparent;
                                background: #f82222;
                                color: #fff;
                                font-size: 18px;
                                text-align: center;
                                line-height: 30px;
                                font-style: normal;
                            }
                        }
                    }
                }
                .footer-right {
                    flex: 0.66;
                    height: 100%;
                    /*width: 494px;!*px*!*/
                    display: flex;
                    .add-shop-car {
                        flex: 1;
                        background: #fa8c35;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 32px;
                        color: #fff;
                        &.forbid-buy {
                            background: #c9c9c9;
                        }
                    }
                    .buy-now {
                        flex: 1;
                        background: #f82222;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 32px;
                        color: #fff;
                        &.forbid-buy {
                            background: #868686;
                        }
                    }
                }
            }
        }
    }

    /*取消收藏弹窗*/
    .cancel-collect {
        .mint-popup {
            width: 100%;
            background: #fff;
            .wait, .cancel {
                height: 88px;
                box-shadow: 0 -1px #e5e5e5 inset;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 32px;
                color: #f00;
            }
            .wait {
                height: 88px;
                box-shadow: none;
                color: #333;
            }
        }
    }

    /*加入购物车的动画*/
    .animate {
        animation: big 1s;
    }

    @keyframes big {
        0% {
            transform: scale(1)
        }
        50% {
            transform: scale(1.3)
        }
        100% {
            transform: scale(1)
        }
    }

</style>


